<template>
  <div id="conteneBox"></div>
</template>

<script>
export default {
  name: 'rightContent',
  data () {
    return {
      userName: 'CMihee'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    #conteneBox{
        box-sizing: border-box;
        width: 100%;
        padding-left: 260px;
        height: 100%;
        background: #ccc;
        position: relative;
        left: 0;
        top: 0;
        color: #fff;
        transition: .3s ease;
    }
</style>



